* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}

body {
  background-image: url('/public/images/staticbackground.webp');
  background-size: cover;
  /* opacity: 0.5; */
}

.home,
.ourmission,
.previousentries,
.newentry,
.sign-up {
  display: flex;
  height: 90vh;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
}

.ourmission {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #000000;
  font-size: 100px;
}
.previousentries {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #000000;
  font-size: 100px;
}

.newentry {
  background-position: center;
  background-size: fill;
  background-repeat: no-repeat;
  color: #000000;
  font-size: 100px;
}

.sign-up {
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  color: #000000;
  font-size: 100px;
}

p1 {
  color: rgb(226, 92, 68);
  font-size: 25px;
  text-align: center;
  align-items: center;
  justify-content: center;
}

p2 {
  color: rgb(226, 92, 68);
  font-size: 20px;
  text-align: center;
  align-items: center;
