.container {
  max-width: 800px;
  margin: 0 auto;
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

h1 {
  text-align: center;
  color: #333;
  font-size: 75px;
}

h2 {
  color: #555;
  margin-top: 20px;
}

p {
  margin-bottom: 20px;
}

ol,
ul {
  margin-bottom: 20px;
}

li {
  margin-bottom: 10px;
}
